        <div class="container-fluid services-details">
            <div class="">
                
            <div class="row justify-content-center align-items-stretch">
                <div class="col-lg-6 bg-light  left-service py-5 ">
                    <div class="">
                        <div class="service-wrapper-2">
                        <p>Financial Planning & Analysis function traditionally helps the CFO’s 
                            office with ongoing strategic and operational visibility on opportunities 
                            and risks to the business. Extending the principles of FP&A beyond the 
                            Finance function to other functions such as Operations, Sales and Marketing 
                            and HR forms the basis of an evolving mindset called Extended Planning & Analysis 
                            abbreviated as XP&A. This was popularized by Gartner’s Research that predicts 
                            about 70% of all fresh FP&A objectives and projects would have an extended scope 
                            beyond Finance function by 2024.  </p>    
                        </div>
                    </div>
                </div>
                <div class="col-lg-6 px-5 left-service text-light justify-content-center align-items-center py-5">
                       <div class="col-lg-8">
                           <p>XP&A's holistic thinking mindset helps Organisations to become more connected. 
                               This is achieved by data standardization, its timely availability and deploying 
                               automated systems. This approach is known as "Connected Planning" which focuses 
                               on connecting all aspects of business planning with each other through these three key drivers
                               <br>
                               <ul>
                                   <li class="py-3">Uniformity across departments regarding information pertaining to projects or opportunities 
                                       available for investment within their respective fields</li>
                                    <li class="py-3">Timeliness at every level so that relevant knowledge can be used efficiently without delay (timely)</li>
                                    <li class="py-3">Accuracy in Insight generation based on algorithms and rules that use a mix of historical data and 
                                        driver-based forecasts </li>
                               </ul>
                            
                            </p>    

                               
                       </div>
