                        <div class="py-5 service-wrapper-2">
                            <div class="col-lg-10 py-3"><h4> <b>Why XP&A is the next leap in performance Management</b></h4></div>
                            <div class="col-lg-10">
                                <p>Businesses are often challenged with data silos which can lead to 
                                longer lead times for report generation and a lack of consolidated data. 
                                Businesses are also struggling with outdated technology and processes 
                                that are causing data silos in the first place.</p>    

                                <p>Not being able to get accurate insights into your company's performance can be costly. 
                                    Your business may not be making decisions based on the most up-to-date information, 
                                    which can lead to lost Revenue and opportunities.  These challenges can prevent finance 
                                    teams from performing deeper analysis and from getting a clear picture of financial and 
                                    operational performance.</p>
                                <p>Finance Function’s Reporting & Insight Challenges</p>
                                    <ul>
                                        <li> <p>Data Silos at User Level</p></li>
                                        <li><p>Consolidation of Data</p></li>
                                        <li><p>Longer lead time for  generating reports</p></li>
                                        <li><p>Deeper analysis of Financial  and Operational Performance</p></li>
                                    </ul>
                            </div>
                        </div>
                </div>
                <div class="col-lg-6 px-5 left-service bg-light text-dark justify-content-center align-items-center py-5">
                        <div class="col-lg-10 d-flex align-items-stretch">
                            <div class=" ">
                                <p>Our Performance Management and Analytical Automation solutions 
                                can help your business overcome these problems. Our solutions 
                                include a single source of truth, a unified database of siloed 
                                data, real-time analytics and insights, agile planning and rolling 
                                forecast modeling, and analytical and process workflow automation. 
                                This will give you the accurate, real-time insights your business 
                                need to make sound business decisions.</p>
                            </div>
                        </div>
                </div>
            </div>
        </div>
